import React from 'react';
import { Link } from './Link';
import { resolve } from 'path';

export const Header = ({ logo, path }) => {
    return (
        <header className="header sticky-top">
            <nav className="navbar navbar-expand-lg navbar-light bg-light py-0 shadow-sm">
                <div className="container">
                    <Link className="navbar-brand" to={path}>
                        <strong>{logo}</strong>
                    </Link>
                    <button className="navbar-toggler">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <div className="navbar-nav nav-tabs ml-auto">
                            <Link exact className="nav-item nav-link" to={path}>Home</Link>
                            <Link className="nav-item nav-link" to={resolve(path, 'books')}>Books</Link>
                            <Link className="nav-item nav-link" to={resolve(path, 'about')}>About</Link>
                            <Link className="nav-item nav-link" to={resolve(path, 'login')}>Login</Link>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
    );
} 